/* ================= PAGE ================= */

body {
  background: #f5f7fb;
  font-family: "Segoe UI", sans-serif;
}

.excel-page {
  max-width: 1100px;
  margin: auto;
  padding: 30px;
}

.header {
  text-align: center;
  margin-bottom: 30px;
}

.logo {
  height: 60px;
  margin-bottom: 10px;
}

/* ================= UPLOAD ================= */

.upload-card {
  background: white;
  border-radius: 14px;
  padding: 30px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.08);
  margin-bottom: 40px;
}

.upload-box {
  border: 2px dashed #f59e0b;
  border-radius: 12px;
  padding: 40px;
  display: block;
  cursor: pointer;
  transition: 0.3s;
}

.upload-box:hover {
  background: #fff7ed;
}

.upload-box input {
  display: none;
}

.upload-content {
  text-align: center;
}

/* ================= GRID ================= */

.file-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px;
}

/* ================= FILE CARD (BEAUTIFIED) ================= */

.file-card {
  background: linear-gradient(180deg, #ffffff, #fafafa);
  padding: 20px;
  border-radius: 18px;
  box-shadow:
    0 10px 25px rgba(0,0,0,0.08),
    inset 0 1px 0 rgba(255,255,255,0.7);
  animation: fadeIn 0.35s ease;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.file-card:hover {
  transform: translateY(-4px);
  box-shadow:
    0 18px 40px rgba(0,0,0,0.12),
    inset 0 1px 0 rgba(255,255,255,0.8);
}

/* LEFT STATUS STRIP */
.file-card.loading {
  border-left: 6px solid #f59e0b;
}

.file-card.success {
  border-left: 6px solid #16a34a;
}

.file-card.error {
  border-left: 6px solid #dc2626;
}

/* ================= CARD HEADER ================= */

.file-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
}

.file-name {
  font-weight: 600;
  font-size: 14px;
  color: #1f2937;
  line-height: 1.4;
  word-break: break-word;
}

/* ================= STATUS PILL ================= */

.status-pill {
  font-size: 11px;
  font-weight: 600;
  padding: 5px 12px;
  border-radius: 999px;
  letter-spacing: 0.4px;
}

.status-pill.loading {
  background: linear-gradient(135deg, #ffedd5, #fed7aa);
  color: #9a3412;
}

.status-pill.success {
  background: linear-gradient(135deg, #dcfce7, #bbf7d0);
  color: #166534;
}

.status-pill.error {
  background: linear-gradient(135deg, #fee2e2, #fecaca);
  color: #7f1d1d;
}

/* ================= PROGRESS ================= */

.progress-track {
  height: 8px;
  background: #e5e7eb;
  border-radius: 999px;
  overflow: hidden;
}

.progress-bar-fill {
  height: 100%;
  background: linear-gradient(
    90deg,
    #fbbf24,
    #f59e0b,
    #fb923c
  );
  transition: width 0.45s ease-in-out;
}

.progress-label {
  margin-top: 8px;
  font-size: 12px;
  color: #6b7280;
  text-align: right;
}

/* ================= SUCCESS & ERROR ================= */

.success-row {
  margin-top: 6px;
  font-size: 13px;
  font-weight: 600;
  color: #166534;
}

.error-code {
  margin-top: 6px;
  font-size: 13px;
  font-weight: 500;
  color: #7f1d1d;
}

/* ================= ANIMATION ================= */

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px) scale(0.98);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
